export default {
	data() {
		return {
			show:false,
			techerName: '',
			index: 1
		}
	},
	methods: {
		backPage () {
			uni.navigateBack({
				delta: 1
			})
		},
		showTeacher (val) {
			this.$data.index = 2
		},
		close () {
			this.$data.index = 1
		},
		changeName (val) {
			if (val == 0) {
				this.techerName = '李铮'
			} else if (val == 1) {
				this.techerName = '海城电子'
			} else if (val == 2) {
				this.techerName = '李凯'
			} else if (val == 3) {
				this.techerName = '肖宝平'
			}
		}
	}
}
